@media (max-width: 767px){
.nav {
    width: auto;
    height: 88px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-evenly;
    border-bottom:1px solid #D9FFF5;
}

.nav-el:nth-child(2) {
    display: none;
}

.nav-el:nth-child(4) {
    display: none;
}

.nav-el:nth-child(5) {
    display: none;
}

.nav-el:nth-child(6) {
    display: none;
}
}

@media (min-width: 768px) {
    .nav {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        height: 88px;
        width: auto;
        }
        
        .nav-el {
        width:40px;
        height: 40px;
        }
        
        .nav:first-child {
        margin-left: 24px;
        }
        
        .nav:last-child {
        margin-right: 24px;
        }
        
        .nav__separator{
        height: 32px;
        width: 2px;
        border-radius: 1px;
        margin: 28px 20px 28px 20px;
        
        }
        
        .nav__logo {
        width: 96.55px;
        height: 50.81px;
        }
        
        .nav__but-call {
        margin-left:auto;
        margin-right: 16px;
        }
        
        .nav__but-chat {
        margin-right:16px;
        }
        
        .nav__but-repair {
        margin-right:16px;
        }
        
        .nav__but-checkstatus {
        margin-right: 64px;
        }
}

@media (min-width: 1336px) {
    .header {
        display: none;
    }
}