

.all-content { 
    height: 100%;
    position: relative;
}

@media (max-width: 767.99px){
.sidebar {
position: fixed;
top: 0;
left: 0;
width: 100%;
min-height: 100%;
overflow-y: auto;
overflow-x: hidden;
background-color: rgba(255, 255, 255, 1);
display: none;
}
    
.sidebar:target {
display: block;
z-index: 10;
}

.modall-1 {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: rgba(255, 255, 255, 1);
    top:0;
    position: fixed;
    display: none;
    }
    
    .modall-1:target{
        display: block;
        z-index: 30;
    }
    
    .modall-2 {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: rgba(255, 255, 255, 1);
    top:0;
    position: fixed;
    display: none;
    }
    
    .modall-2:target{
        display: block;
        z-index: 30;
    }
}

@media (min-width: 767px) and (max-width: 1355px){
    .sidebar {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        min-height: 100%;
        background-color: rgba(255, 255, 255, 0.8);
        display: none;
        overflow-y: auto;
        overflow-x: hidden;
        overflow: scroll;
        }
            
        .sidebar:target {
        display: block;
        z-index: 10;
        }

        .sidebar__area {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
        }

.modall-1 {
width: 100%;
height: 100%;
overflow-y: auto;
overflow-x: hidden;
background-color: rgba(255, 255, 255, 0.8);
top:0;
right: 0;
position: fixed;
display: none;
}

.modall-1:target{
    display: block;
    z-index: 30;
}

.div-for-position {
    position: fixed;
    top:0;
    right: 0;
}

.modall-2 {
width: 100%;
height: 100%;
overflow-y: auto;
overflow-x: hidden;
background-color: rgba(255, 255, 255, 0.8);
top:0;
right: 0;
position: absolute;
display: none;
}

.modall-2:target{
    display: block;
    z-index: 30;
}
}

