.section-1 {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    @media (max-width: 1335px){
    align-items: center;
}
    @media (min-width: 1336px) {
    align-items: baseline;
    }
}

.section-1__highlight {
    margin: 0;
    height: 40px;
    width: 4px;
    left: 4px;
    top: 112px;
    margin-top: 24px;
}

.section-1__text {
    height: 40px;
    width: 288px;
    left: 16px;
    top: 112px;
    border-radius: nullpx;
    font-family: 'TTLakes';
    margin-left: 12px;
    margin-top: 24px;
    white-space: nowrap;
}

.section-1__maintext {
    margin: 0;
}

.section-1-button-repair {
    display: none;
}

.section-1-button-checkstatus {
    display: none;
}

@media (min-width: 1336px){
    .section-1 {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: baseline;
        
    }
    
    .section-1__highlight {
        margin: 0;
        height: 40px;
        width: 3px;
        left: 4px;
        top: 112px;
        margin-top:18px;
        align-self: center;
    }
    
    .section-1__text {
        height: 40px;
        width: 288px;
        left: 16px;
        top: 112px;
        border-radius: nullpx;
        font-family: "TTLakes";
        margin-left: 12px;
        margin-top: 24px;
    }
    
    .section-1__maintext {
        margin: 0;
        font-size: 28px;
        line-height: 40px;
        letter-spacing: -0.6000000238418579px;
        text-align: left;
    
    }
    
    .section-1-button-repair {
        display: flex;
        font-family: "TTLakes";
        margin-left: auto;
        align-items: center;
    }
    
    .section-1-button-checkstatus {
        display: flex;
        font-family: "TTLakes";
        margin-left: 32px;
        margin-right: 32px;
        align-items: center;
        
    }
    
    .section-1-button__img {
        width: 40px;
        height: 40px;
        margin-left: 9px;
    }

    .header-main{
        display: none;
    }
}
