.section-6 {
    position: relative;
    background: linear-gradient(270deg, #F8F8F8 0%, #FFFFFF 100%);
    min-width: 320px;
    height: 80px;
    border-top: 1px solid #D9FFF5;
}

.section-6__text {
    font-family: "TTLakes";
    height: 48px;
    width: 288px;
    left: 16px;
    top: 16px;
    position: absolute;
    color: #7E7E82;
    font-size: 16px;
    line-height: 24px;
    font-weight: normal;
    font-style: normal;
    margin:0;
    white-space: wrap;
}

.btn-pink {
    background-color: #FF3E79;
    min-height: 32px;
    min-width: 119px;
    border-radius: 16px;
    position: relative;
}
.btn-pink__text{
    position: absolute;
    color: white;
    top: 4px;
    right: 40px;
    bottom: 4px;
    left: 12px;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.15000000596046448px;
    text-align: right;
    margin-top: 0;
    font-family: "TTLakes";
}

.btn-pink__ico{
    position: absolute;
    top: 11.37px;
    right: 11px;
    bottom: 10px;
    left: 96px;
}

.table-header {
    display: none;
    font-family: "TTLakes";
}

.row {
    background-color: white;
    padding: .5em 1.5em .75em 1.5em;
    position: relative;
    box-sizing: border-box;
    font-family: "TTLakes";

}

.row dt {
    font-family: 'TTLakes';
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: left;
    color: #7E7E82;
    padding-top: .5em;
}

 .row dd {
    font-family: 'TTLakes';
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.20000000298023224px;
    text-align: left;
    margin-left: 0px;

}

 .row .order {
    display: flex;
    justify-content: space-between;
    font-family: 'TTLakes';
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.20000000298023224px;
    text-align: left;
}

.order button {
    margin-top: -1em;
    width: 60px;
}

.price {
    flex-grow: 2;
    width: 60px;
}

.row-label {
    width: 360px;
}

@media (max-width: 786px){
    .row-label {
        width: auto;
    }
.row { 
    width: 260px;
    border-radius: 6px;
    margin-left: 16px;
    height: 200px;
    margin-bottom: 16px;
}

.slider-3 {
    margin-bottom: 16px;
}

