    left: 96px;
}

}

@media (min-width: 768px) { 
    .table {
min-width: 720px;
border-radius: 6px;
margin: 0 24px 0 24px;

    }
 .table-header {
        display: flex;
        margin: 24px;

    }

 .row dt {
        display: none;
    }

 .row  {
        display: flex;
        height: 64px;
        padding-bottom: 0px;
        margin-bottom: 0px;
        padding-top: 0px;
        margin-top: 0px;
        border-bottom: 1px solid #EAEAEA;
        vertical-align: middle;
        text-align: center;

  
    }

.row-label {
        flex-grow: 2;
    }


 .row .order,
 .table-header .order {
        width: 30%;
    }

 .row .order button {
        margin-top: -.5em;
    }

