@media (max-width: 767.99px){
.main-for-width {
    max-width: 768px;
    height: 1722px;
    position: relative;
    padding: 0;
    margin: 0;
}

.content {
    min-width: 320px;
    height: 1024px;
    position: relative;
    background: rgba(255, 255, 255, 1);
}


.navigation__burger {
    position: absolute;
    top:23px;
    right: 256px;
    bottom: 961px;
    left: 24px;
}

.navigation__logo {
    height: 51px;
    width: 97px;
    position:absolute;
    top: 18.73px;
    right: 142.27px;
    bottom: 954.27px;
    left: 80.73px;
    margin: auto;
}

.navigation__search {
    position: absolute;
    top: 23px;
    right: 24px;
    bottom: 961px;
    left: 256px;
    margin-left: auto;
}

.main-menu {
    position: relative;
    top:116px;
    width: 222px;
    height: 876px;
}

.main-menu__highlight {
    height: 32px;
    width: 4px;
    left: 7px;
    top: 116px;
}

.main-menu__heading-text {
    position: absolute;
    top: 4px;
    right: 78px;
    bottom: 848px;
    left: 24px;
    margin: 0;
    
}

.main-menu__heading-text h1 {
    font-family: "TTLakes";
    color: rgba(126, 126, 130, 1);
    font-weight: normal;
    font-style: normal;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
    margin: 0;
    white-space: nowrap;
}

.main-menu-options {
    padding:0;
}

.main-menu__option {
    font-family: "TTLakes";
    box-sizing: border-box;
    margin: 32px 0 0 24px; 
    list-style: none;
    font-weight: 100;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.20000000298023224px;
}

.main-menu__option:hover{
    filter: opacity(60%);
    font-weight: bolder;
    white-space: nowrap;
}

.main-menu__option a{
    text-decoration: none;
    color: rgba(27, 28, 33, 1);
}

.main-menu__call {
    position: absolute;
    top: 688px;
    right: 158px;
    bottom: 148px;
    left: 24px;
}

.main-menu__chat {
    position: absolute;
    top: 688px;
    right: 102px;
    bottom: 148px;
    left: 80px;
}

.main-menu__profile {
    position: absolute;
    top: 688px;
    right: 46px;
    bottom:148px;
    left: 136px;
}

.content-buttons:hover {
    transform:scale(1.2);
}

.main-menu__email {
    position:absolute;
    font-family: "TTLakes";
    font-size: 16px;
    line-height: 24px;
    font-weight: bolder;
    top: 744px;
    right: 92;
    bottom: 108px;
    left: 24px;
}

.main-menu__telephone-number {
    position: absolute;
    font-family: "TTLakes";
    font-size: 24px;
    font-weight: bold;
    top: 780px;
    right: 2px;
    bottom: 64px;
    left: 24px;
    white-space: nowrap;
}

.main-menu-lang-of-page{
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.main-menu-lang {
    font-family: "TTLakes";
    font-size: 16px;
    line-height: 24px;
    text-decoration: none;
    color: rgba(27, 28, 33, 1);
    position:absolute;
    top:852px;
}

.main-menu-lang__ru {
    right: 173px;
    left: 24px;
}

.main-menu-lang__en {
    right: 142px;
    left: 56px;
}

.main-menu-lang__ch {
    right: 111px;
    left: 87px;
}

.main-menu__lang-active {
    color: rgba(126, 126, 130, 1);
}

.main-menu-lang:hover:not(.main-menu__lang-active){
    font-weight: bolder;
}

.content-buttonss {
    list-style: none;
}

.menu-lang > li {
    list-style: none;
}
}
