@media(max-width: 1335px){
.section-2 {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-content: stretch;
    overflow: auto;
    white-space: pre-wrap;
}

.section-2__service-info {
font-family: "TTLakes";
font-size: 16px;
line-height: 24px;
letter-spacing: -0.20000000298023224px;
text-align: left;
white-space: nowrap;
color: #7E7E82;
text-decoration: none;
font-weight: normal;
margin-left:12px;
margin-top: 32px;
padding: 8px 9px 8px 9px;
border: 2px solid #F8F8F8;
}

.section-2__service-info:hover {
    border: 2px solid #B8FFEC;
    border-radius: 6%;
}

.service-info-1 {
    display: none;
}
}
@media (min-width: 1336px){
    .section-2 {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-content: stretch;
        white-space: pre-wrap;
        margin-top: 32px;
        flex-wrap: wrap;
    }
    
    .section-2__service-info {
    font-family: "TTLakes";
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.20000000298023224px;
    text-align: left;
    white-space: nowrap;
    color: #7E7E82;
    text-decoration: none;
    font-weight: normal;
    margin-left: 16px;
    margin-bottom: 0;
    padding: 8px 9.3px 8px 16.54px;
    border: 2px solid #F8F8F8;
    }
    
    .service-info-1 {
        order: 1;
    }
    
    .service-info-2 {
        order: 2;
    }
    
    .service-info-3 {
        order:3;
    }
    
    .service-info-4 {
        order:4;
    }
    
    .service-info-5 {
        order: 6;
    }
    
    .service-info-6 {
        order: 5;
    }
    
    .service-info-7 {
        order: 7;
    }
    
    .section-2__service-info:hover {
        border: 2px solid #B8FFEC;
        border-radius: 6%;
        background-color: #FFFFFF;
    }
    
    .section-2__service-info:nth-child(3){
        display: none;
    }
}