@media (max-width: 767.99px){
.footer {
    display: flex;
    flex-direction: column;
}

.footer__text {
    color: #7E7E82;
    font-family: 'TTLakes';
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: center;
    margin-top: 16px;
}

.footer__text:last-child{
    margin-bottom: 19px;
}
}

@media (min-width: 768px){
    .footer {
        display: flex;
        flex-direction: row;
        height: 105px;
    }
    
    .footer__text {
        color: #7E7E82;
        font-family: 'TTLakes';
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0px;
        text-align: left;
        margin-top: 32px;
        margin-left: 24px;
        justify-content: baseline;
    }

    .footer__text:first-child {
        width: 218px;
        height: 32px;
        white-space: normal;
    }

    .footer__text:nth-child(2){
        text-align: center;
        margin: auto;
    }
    
    .footer__text:last-child{
        margin-bottom: 19px;
        text-align: right;
        width: 213px;
        height: 48px;
        margin-right: 33px;
    }
}

@media (min-width: 1336px) {
    .footer {
        display: flex;
        flex-direction: row;
        height: 85px;
    }
    
    .footer__text {
        color: #7E7E82;
        font-family: 'TTLakes';
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0px;
        text-align: left;
        margin-top: 32px;
        margin-left: 24px;
        justify-content: baseline;
    }

    .footer__text:first-child {
        width: 218px;
        height: 32px;
        white-space: normal;
    }

    .footer__text:nth-child(2){
        text-align: center;
        margin: auto;
    }
    
    .footer__text:last-child{
        margin-bottom: 19px;
        text-align: right;
        width: 330px;
        height: 32px;
        margin-right: 33px;
    }
}