@media(max-width: 767.99px){
.modall-feedback {
    width:330px;
    height: 1886.59px;
    background: white;
    z-index: 120;
    min-width: 700px;
    margin: 0;
    padding: 0;
    position: relative;
    display: flex;
    flex-direction: column;
}

.modall-feedback__highlight {
    height: 40px;
    width: 4px;
    left: 4px;
    top: 115px;
    position: absolute;
    
}

.modall-feedback-text__main-text {
    height: 32px;
    width: 288px;
    left: 16px;
    top: 104px;
    font-family: "TTLakes";
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6000000238418579px;
    text-align: left;
    position: absolute;
}

.modall-feedback-inputs {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 376px;
    margin-left: 16px;
    margin-right: 16px;
    margin-top: 160px;
    padding-top: 24px;
}
.modall-feedback__input {
    height: 48px;
    width: 288px;
    border-radius: 8px;
    border: 1px solid #EAEAEA;
    color: #FFFFFF;
    font-family: "TTLakes";
    font-family: TT Lakes;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.20000000298023224px;
    text-align: left;
    margin-top: 16px;

}

.modall-feedback__input:first-child{
    margin-top: 0px;
}

.modall-feedback__text {
    position: absolute;
    height: 64px;
    width: 288px;
    left: 16px;
    top: 486px;
    font-family: "TTLakes";
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: left;


}

.modall-feedback__link {
    color: #FF3E79;
}

.modall-feedback-btn-pink {
    height: 32px;
    width: 136px;
    border-radius: 16px; 
    background-color: #FF3E79;
    margin-top: 125px;
    margin-left: 168px;
    position: relative;
}

.modall-feedback-btn-pink__text{
    position: absolute;
    color: white;
    top: 4px;
    right: 40px;
    bottom: 4px;
    left: 12px;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.15000000596046448px;
    text-align: right;
    margin-top: 0;
    font-family: "TTLakes";
}

.modall-feedback-btn-pink__ico{
        margin-left: 15px;
        position: absolute;
        top: 11px;
        right: 19px;
        bottom: 10px;
        left: 96px;
    }
}

