@media (max-width: 767px){
    
    .section-4 {
        position: relative;
        background: linear-gradient(270deg, #F8F8F8 0%, #FFFFFF 98.61%);
        min-width: 320px;
        height: 80px;
        border-top: 1px solid #D9FFF5;
    }
    
    .section-4__text {
        font-family: "TTLakes";
        height: 48px;
        width: 288px;
        left: 16px;
        top: 16px;
        position: absolute;
        color: #7E7E82;
        font-size: 16px;
        line-height: 24px;
        font-weight: normal;
        font-style: normal;
        margin:0;
        white-space: wrap;
    }
    
    .slider {
        height: 240px;
        min-width: 320px;
        background-color: #F2F2F2;
    }
    
    .cards {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        white-space: nowrap;
    }
    
    .card {
        max-height: 72px;
        max-width: 240px;
        left: 32px;
        top: 80px;
        border-radius: 6px;
        list-style: none;
        background: #FFFFFF;
        border: 1px solid #EAEAEA;
        margin-left: 32px;
        margin-bottom: 16px;
        position: relative;
    }
    
    
    .card-but-go {
        position:absolute;
        height: 40px;
        width: 40px;
        top: 16px;
        right: 16px;
        bottom: 16px;
        left: 184px;
    }
    
    .card-bttn-go:hover {
        transform:scale(1.2);
    }
    
    
    .card__lenovo {
        position: absolute;
        top: 20px;
        right: 116px;
        bottom: 20px;
        left: 16px;
    }
    
    .card__samsung {
        position: absolute;
        top: 17px;
        right: 108px;
        bottom: 16.48px;
        left: 16px;
    }
    
    .card__apple {
        position: absolute;
        top: 12px;
        right: 180px;
        bottom: 16px;
        left: 16px;
    }
    
    .card__view-sonic {
        position: absolute;
        top: 26px;
        right: 98px;
        bottom: 25px;
        left: 14px;
    }
    
    .card__bosch {
        position: absolute;
        top: 21.7px;
        right: 96px;
        bottom: 21.7px;
        left: 16px;
    }
    
    .card__hp {
        position: absolute;
        top: 10px;
        right: 178px;
        bottom: 10px;
        left: 10px;
    }
    
    .card__acer {
        position: absolute;
        top: 24px;
        right: 130px;
        bottom: 24px;
        left: 16px;
    }
    
    .card__sony {
        position: absolute;
        top: 23px;
        right: 98px;
        bottom: 23.4px;
        left: 14px;
    }
    
    .read-more {
        background-color: #F2F2F2;
        padding-top: 0px;
        position: relative;
        display: none;
    }
    
    .read-more__a {
        font-family: "TTLakes";
        font-size: 16px;
        font-weight: bolder;
        line-height: 24px;
        letter-spacing: -0.20000000298023224px;
        text-decoration: none;
        color: #1B1C21;
    }
        
    .read-more__icon{
        margin-right: 15px;
        transform: rotate(180deg);
    }
        
    .read-more__icon--close {
        transform: rotate(360deg);
    }
    
    }
        
