@media (max-width: 767.99px) {
.modall-call {
    height: 676px;
    background: white;
    width: 320px;
    height: 1886.59px;
    position: relative;
    margin: 0;
    padding: 0;
}

.modall-call__highlight {
    height: 40px;
    width: 4px;
    left: 4px;
    top: 115px;
    position: absolute;
}

.modall-call-text__main-text {
    height: 32px;
    width: 288px;
    left: 16px;
    top: 104px;
    font-family: "TTLakes";
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6000000238418579px;
    text-align: left;
    position: absolute;
}

.modall-call__input {
    height: 48px;
    width: 288px;
    left: 16px;
    top: 160px;
    border-radius: 8px;
    border: 1px solid #EAEAEA;
    color: #FFFFFF;
    position: absolute;
    font-family: "TTLakes";
    margin-left: 16px;
    margin-right: 16px;
    font-family: TT Lakes;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.20000000298023224px;
    text-align: left;
    padding-left: 16px;

}

.modall-call__text {
    position: absolute;
    height: 64px;
    width: 288px;
    left: 16px;
    top: 224px;
    font-family: "TTLakes";
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: left;


}

.modall-call__link {
    color: #FF3E79;
}

.modall-call-btn-pink {
    background-color: #FF3E79;
    min-height: 32px;
    width: 136px;
    top: 306px;
    left: 168px;
    border-radius: 16px;
    position: relative;
}

.modall-btn-pink__text{
    position: absolute;
    color: white;
    top: 4px;
    right: 40px;
    bottom: 4px;
    left: 12px;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.15000000596046448px;
    text-align: right;
    margin-top: 0;
    font-family: "TTLakes";
}

.modall-btn-pink__ico{
    margin-left: 15px;
        position: absolute;
        top: 11px;
        right: 19px;
        bottom: 10px;
        left: 96px;
}
}

